import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import Input from "../../components/Input/Input";
import Button from "../../components/Buttons/Button";
import { Redirect } from "react-router-dom";
import * as routes from "../../helpers/routes";
import * as api from "../../API/API";
import "./SettingsPage.css";

class SettingsPage extends Component {
  state = {
    changeEmail: "",
    changePassword: "",
    firstName: "",
    lastName: "",
    redirect: false
  };

  inputChangeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  changePassword = e => {
    e.preventDefault();
    api
      .changePassword(this.props.token, this.state.changePassword)
      .then(res => this.setState({ redirect: true }))
      .catch(err => console.log(err));
  };
  logout = () => {
    this.setState({ redirect: true });
    this.props.logout();
  };
  changeEmail = e => {
    e.preventDefault();
    api
      .changeEmail(this.props.token, this.state.changeEmail)
      .then(res => {
        this.props.setNewEmail(this.state.changeEmail);
        this.setState({ redirect: true });
      })
      .catch(err => console.log(err));
  };
  changeUserName = e => {
    e.preventDefault();
    api
      .setUserInfo(
        this.props.token,
        this.state.firstName + " " + this.state.lastName
      )
      .then(res => {
        this.props.setNewName(this.state.firstName + " " + this.state.lastName);
        this.setState({ redirect: true });
      })
      .catch(err => console.log(err));
  };
  render() {
    return (
      <div className="SettingsPage">
        {this.state.redirect && <Redirect to={routes.home} />}
        <h1 className="SettingsPage__heading">Settings</h1>
        <form className="SettingsPage__form" onSubmit={this.logout}>
          <p>
            <span style={{ fontWeight: "bold" }}>Email: </span>
            {this.props.userEmail}
          </p>
          <p>
            <span style={{ fontWeight: "bold" }}>Display Name: </span>
            {this.props.userName}
          </p>
          <Button btnType="Info">Logout</Button>
        </form>

        <form className="SettingsPage__form" onSubmit={this.changeEmail}>
          <h2>Change Email </h2>
          <div className="SettingsPage__input">
            <Input
              classys="Input Input-blue"
              name="changeEmail"
              value={this.state.changeEmail}
              changed={e => this.inputChangeHandler(e)}
              placeholder="Enter email ..."
              type="email"
            />
          </div>
          <Button btnType="Info">Change</Button>
        </form>

        <form className="SettingsPage__form" onSubmit={this.changePassword}>
          <h2>Change Password </h2>
          <div className="SettingsPage__input">
            <Input
              classys="Input Input-blue"
              name="changePassword"
              value={this.state.resetPassword}
              changed={e => this.inputChangeHandler(e)}
              placeholder="Enter password ..."
              type="password"
            />
          </div>
          <Button btnType="Info">Change</Button>
        </form>

        <form className="SettingsPage__form" onSubmit={this.changeUserName}>
          <h2>Change Password </h2>
          <div className="SettingsPage__input">
            <Input
              classys="Input Input-blue"
              name="firstName"
              value={this.state.firstName}
              changed={e => this.inputChangeHandler(e)}
              placeholder="Enter first name ..."
              type="text"
            />
          </div>{" "}
          <div className="SettingsPage__input">
            <Input
              classys="Input Input-blue"
              name="lastName"
              value={this.state.lastName}
              changed={e => this.inputChangeHandler(e)}
              placeholder="Enter last name ..."
              type="text"
            />
          </div>
          <Button btnType="Info">Change</Button>
        </form>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    token: state.token,
    userEmail: state.userEmail,
    userName: state.userName,
    refreshToken: state.refreshToken
  };
};
const mapDispatchToProps = dispatch => {
  return {
    logout: () => dispatch(actions.logout()),
    setNewEmail: email => dispatch(actions.setUserEmail(email)),
    setNewName: name => dispatch(actions.setUserName(name))
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SettingsPage);
