import * as actionTypes from "./actionTypes";
import { signInUser, getNewToken } from "../API/API";

export const login = (email, password) => {
  return dispatch => {
    signInUser(email, password)
      .then(res => {
        console.log(res);
        dispatch(loginSuccess(res.data.localId));
        dispatch(setUserToken(res.data.idToken, res.data.refreshToken));
        dispatch(setUserEmail(res.data.email));
        dispatch(setUserName(res.data.displayName));
        setInterval(() => {
          dispatch(refreshOldToken(res.data.refreshToken));
        }, 3400000);
      })
      .catch(err => dispatch(loginFail(err)));
  };
};

export const refreshOldToken = refreshToken => {
  return dispatch => {
    getNewToken(refreshToken).then(res =>
      dispatch(setUserToken(res.data.id_token, res.data.refresh_token))
    );
  };
};

export const resetErrorMessage = () => {
  return { type: actionTypes.RESET_ERROR };
};

export const setUserToken = (token, refreshToken) => {
  const tokenExpiryTime = new Date();
  tokenExpiryTime.setHours(tokenExpiryTime.getHours() + 1);
  return {
    type: actionTypes.SET_USER_TOKEN,
    token: token,
    refreshToken: refreshToken,
    expiryDate: tokenExpiryTime.toString()
  };
};

export const setUserEmail = userEmail => {
  return {
    type: actionTypes.SET_USER_EMAIL,
    userEmail: userEmail
  };
};

export const setUserName = userName => {
  return {
    type: actionTypes.SET_USER_NAME,
    userName: userName
  };
};
export const loginSuccess = userId => {
  return {
    type: actionTypes.LOGIN_SUCCESS,
    userId: userId
  };
};
export const loginFail = error => {
  return {
    type: actionTypes.LOGIN_FAIL,
    error: error.response.data.error.message
  };
};

export const logout = () => {
  return { type: actionTypes.LOGOUT };
};
export const authReset = () => {
  return { type: actionTypes.AUTH_RESET };
};
